@import "vars";
@import "libs";
@import "fonts";

::placeholder {
	color: #999; }
html, body {
	height: 100%; }
.main {
	flex: 1 0 auto; }
html {
	font-size: 2.67vw; }
body {
	font-size: 1.4rem;
	min-width: 320px;
	position: relative;
	line-height: 1.57;
	font-family: 'Muli', sans-serif;
	overflow-x: hidden;
	color: $text;
	display: flex;
	flex-direction: column;
	input, textarea {
		display: block;
		width: 100%;
		outline: none;
		resize: none;
		border: none; }
	input, textarea, button, a {
		&:active, &:hover, &:focus {
			outline: 0;
			outline-offset: 0; } }
	button {
		cursor: pointer;
		background-color: transparent;
		border: none;
		padding: 0; } }
b, strong {
	font-weight: 700; }
p {
	margin-bottom: 0; }
a {
	color: $accent;
	text-decoration: none;
	transition: 0.25s;
	&:hover {
		color: inherit;
		text-decoration: underline; }
	&[href^="#"], &[href^="tel"], &[href^="mail"] {
		&:hover {
			text-decoration: none; } }
	&.link-decorated {
		text-decoration: underline;
		font-size: 1.7rem;
		&:hover {
			text-decoration: none; } } }
.w-100 {
	width: 100%; }
.h-100 {
	height: 100%; }
ul, ol {
	@include list-unstyled; }
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	display: block;
	line-height: 1.2;
	font-weight: 700;
	font-family: 'Teko', sans-serif;
	margin-bottom: 1.6rem; }
h1, .h1 {
	font-size: 3.6rem; }
h2, .h2 {
	font-size: 3rem; }
h3, .h3 {
	font-size: 2.6rem; }
h4, .h4 {
	font-size: 2.2rem; }
h5, .h5 {
	font-size: 1.8rem; }
h6, .h6 {
	font-size: 1.6rem; }
.text {
	&-xs {}
	&-s {}
	&-m {}
	&-l {}
	&-xl {}
	&-gray {
		color: $gray; } }
.btn {
	display: flex;
	cursor: pointer;
	transition: 0.25s;
	justify-content: center;
	align-items: center;
	text-decoration: none!important;
	&-m {
		height: 4rem;
		border-radius: 0.4rem;
		font-size: 1.5rem;
		padding: {
			left: 3.5rem;
			right: 3.5rem; } }
	&-blue {
		background-color: $accent;
		color: #ffffff;
		&:hover {
			background-color: darken($accent,5%); } }
	&-white {
		background-color: #ffffff;
		color: $accent;
		border: .15rem solid $accent;
		&:hover {
			background-color: $accent;
			color: #ffffff; } } }
.label {
	display: flex;
	align-items: center;
	justify-content: center;
	transition: 0.25s;
	margin-bottom: 0;
	&-icon {
		&.label-m {
			width: 2.4rem;
			flex: 0 0 2.4rem;
			max-width: 2.4rem;
			padding: 0; } }
	&-s {}
	&-m {
		border-radius: 0.3rem;
		height: 2.4rem;
		padding: {
			left: 1rem;
			right: 1rem; } }
	&-l {}
	&-pink {
		background-color: #ebdcff; }
	&-blue {
		background-color: #d2e6ff; } }
.group {
	&-btn {
		display: flex;
		justify-content: center;
		align-items: center;
		> *:not(:last-child) {
			margin-right: 1.2rem; } } }
.icon {
	display: inline-block;
	vertical-align: middle;
	top: -0.3rem;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	&-left {
		margin-right: .8rem; }
	&-right {
		margin-left: .8rem; }
	&-xs {
		width: 1.2rem;
		height: 1.2rem; }
	&-s {
		width: 1.6rem;
		height: 1.6rem; }
	&-m {
		width: 2rem;
		height: 2rem; }
	&-bullet {
		border-radius: 100%;
		background-color: #34e0a1; } }
.image {
	display: block;
	background-position: center;
	background-repeat: no-repeat;
	position: relative;
	&-con {
		background-size: contain; }
	&-cov {
		background-size: cover; }
	&-hover {
		overflow: hidden;
		cursor: pointer;
		> * {
			position: relative;
			z-index: 2; }
		&:before {
			content: '';
			width: 100%;
			height: 100%;
			background: inherit;
			position: absolute;
			top: 0;
			left: 0;
			transition: 0.4s; }
		&:hover:before {
			transform: scale(1.05, 1.05); } } }
.hidden {
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	pointer-events: none; }
.container {
	margin: 0 auto;
	max-width: 1600px; }
.row {
	display: flex;
	flex-wrap: wrap; }
.form {
	&-row {
		@extend .row;
		.form {
			&-group {
				flex-basis: 0;
				flex-grow: 1;
				max-width: 100%; }
			&-submit {
				flex: 0 0 auto;
				width: auto;
				max-width: none; } } }
	&-control {
		height: 4rem;
		border-radius: 0.4rem;
		background-color: #fff;
		font-size: 1.3rem;
		padding: {
			left: 1.6rem;
			right: 1.6rem; } } }
.heading {
	display: flex;
	justify-content: space-between;
	margin-bottom: 1.6rem;
	align-items: center;
	&-text {
		display: flex;
		flex-wrap: wrap;
		flex-basis: 0;
		flex-grow: 1;
		max-width: 100%;
		> * {
			margin-bottom: 0; } }
	&-link {
		flex: 0 0 auto;
		width: auto;
		max-width: none; } }
.section {
	margin-bottom: 4.8rem;
	&-text {
		flex: 0 0 100%;
		max-wdith: 100%; }
	&-content {
		flex: 0 0 100%;
		max-wdith: 100%;
		.btn {
			margin: 0 auto; } } }
@media screen and (max-width: 768px) {
	.section-text {
		.btn {
			display: none; } } }
.blockquote {
	&-box {
		display: flex;
		flex-wrap: wrap; }
	&-block {
		border-radius: .8rem;
		background-color: #ffffff;
		padding: 2rem;
		box-shadow: 0 .2rem 1rem 0 rgba(136, 155, 190, 0.7);
		.h4 {
			margin-bottom: 0.8rem; }
		.author {
			margin-bottom: -4.6rem;
			display: flex;
			margin-top: 1.6rem;
			&-image {
				&:before {
					content: '';
					width: 10.4rem;
					height: 10.4rem;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					position: absolute;
					z-index: -1;
					display: block;
					background-color: #ffffff;
					box-shadow: 0 .2rem 1rem 0 rgba(136, 155, 190, 0.7);
					border-radius: 100%; } } } } }
.author {
	display: flex;
	&-image {
		flex: 0 0 9.6rem;
		max-width: 9.6rem;
		position: relative;
		margin-right: 1.6rem;
		&:before {
			content: '';
			width: 100%;
			height: 100%;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			position: absolute;
			z-index: -1;
			display: block;
			background-color: #ffffff;
			box-shadow: 0 .2rem 1rem 0 rgba(136, 155, 190, 0.7);
			border-radius: 100%; }
		.image {
			padding-bottom: 100%;
			border-radius: 100%; } }
	&-text {
		.text-gray {
			display: block;
			margin-top: 1rem; } } }
.rating {
	display: flex;
	align-items: center;
	.rating-block {
		flex: 0 0 50%;
		max-wdith: 50%; }
	&-block {
		display: block;
		border-radius: .4rem;
		box-shadow: 0 .2rem 1rem 0 rgba(136, 155, 190, 0.7);
		background-color: #ffffff;
		padding: 1.6rem;
		text-align: center;
		color: $text;
		&:not(:first-child) {
			margin-left: 0.7rem; }
		&_logo {
			@include img-fluid(); } }
	&-list {
		display: flex;
		margin-bottom: 1.2rem; }
	&-item {
		&:not(:first-child) {
			margin-left: .2rem; } } }
// header
.header {
	padding: {
		top: 0.8rem;
		bottom: 0.8rem; }
	.container > .row {
		align-items: center;
		padding: {
			left: 2rem;
			right: 2rem; } }
	.hamburger {
		margin-right: 1.6rem; }
	.search {
		margin-left: auto; }
	.account {
		margin-left: 2.4rem; } }
// header

// owl
.owl {
	&-nav {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 0;
		right: 0; }
	&-dots {
		position: absolute;
		bottom: -1.1rem;
		left: 0;
		right: 0;
		display: flex;
		justify-content: center; }
	&-dot {
		display: block;
		width: 2.4rem;
		height: 0.3rem;
		border-radius: .1rem;
		background-color: rgba(0, 179, 240, 0.2)!important;
		transition: 0.25s;
		&.active {
			background-color: #00b3f0!important; }
		&:not(:first-child) {
			margin-left: 0.4rem; }
		span {
			display: none; } } }
// owl

// promo
.promo {
	margin-bottom: 5.7rem;
	&-block {
		&_content {
			height: 88vw;
			max-height: 65vh;
			color: #ffffff;
			display: flex;
			flex-direction: column;
			justify-content: flex-end;
			padding: 3.2rem 3.2rem 4rem;
			text-align: center;
			.h1 {
				margin-bottom: 1.2rem; }
			form {
				position: relative; }
			.form-control {
				height: 4.8rem;
				padding-left: 5.2rem; }
			.btn {
				position: absolute;
				left: 2.6rem;
				transform: translate(-1rem, -50%);
				top: 50%; } } } }
// promo

// benefits
.benefits {
	background-color: rgba(#00b3f0, .1);
	margin-bottom: 5.1rem;
	&-carousel {
		transform: translateY(-2.6rem); }
	&-block {
		text-align: center;
		padding: {
			left: 2rem;
			right: 2rem; }
		&_icon {
			margin-bottom: 0.6rem;
			.icon {
				width: 5.2rem;
				height: 5.2rem; } } }
	.group-btn {
		transform: translateY(2rem); } }
// benefits

// story
.story {
	margin-bottom: 4.3rem;
	&-block {
		height: 101vw;
		text-align: center;
		color: #ffffff;
		padding: 3.2rem 2rem;
		&:hover {
			color: #ffffff;
			.story-more .icon {
				transform: translateX(.5rem); } } }
	&-more {
		display: block;
		text-align: center;
		margin-top: 2.2rem; } }
// story

// offers
.offers {
	margin-bottom: 4.8rem;
	padding: {
		left: 2rem;
		right: 2rem; }
	&-block {
		border-radius: 0.4rem;
		box-shadow: 0 .1rem .5rem 0 rgba(136, 155, 190, 0.5);
		display: flex;
		color: $text;
		&:nth-child(n+4) {
			display: none; }
		&:not(:first-child) {
			margin-top: 0.8rem; }
		&:hover {
			.image:before {
				transform: scale(1.05, 1.05); } }
		&_image {
			flex: 0 0 12rem;
			max-width: 12rem;
			border-top-left-radius: 0.4rem;
			border-bottom-left-radius: 0.4rem;
			overflow: hidden;
			.image {
				padding-bottom: 112%; } }
		&_text {
			padding: 1.2rem;
			display: flex;
			flex-direction: column; }
		&_date {
			margin-bottom: 0.6rem;
			font-size: 1.2rem;
			text-transform: uppercase;
			.text-gray {
				display: block; } }
		&_info {
			display: flex;
			justify-content: space-between;
			margin-top: auto;
			align-items: center;
			.h6 {
				margin-bottom: 0; } } } }
@media screen and (max-width: 768px) {
	.offers-carousel {
		display: block; } }
// offers

// opinion
.opinion {
	background-color: rgba(0, 179, 240, 0.1);
	padding: 4rem 2rem 4.8rem;
	position: relative;
	.rating {
		margin: {
			top: 7rem;
			bottom: 3.2rem; }
		&-list {
			justify-content: center; } } }
// opinion

// orgnisations
.organisations {
	padding: {
		left: 2rem;
		right: 2rem; }
	margin-bottom: 4.8rem;
	&-box {
		display: flex;
		overflow-x: auto;
		margin-top: 2.6rem;
		padding: {
			bottom: 2rem; }
		margin: {
			right: -2rem;
			left: -2rem; }
		> *:not(:first-child) {
			margin-left: 1.6rem; }
		&:after, &:before {
			content: '';
			display: block;
			flex: 0 0 2rem;
			max-width: 2rem;
			min-height: .1rem; } }
	&-block {
		flex: 0 0 auto;
		width: auto;
		max-width: none;
		text-align: center;
		display: block;
		color: $text;
		padding-bottom: 1rem;
		&:not(:first-child) {
			margin-left: 3rem; }
		&_image {
			margin-bottom: 1.4rem;
			img {
				@include img-fluid;
				max-height: 8.4rems; } }
		&_title {
			text-transform: uppercase;
			font-size: 1.3rem; } } }
// organisations

// premium
.premium {
	padding: 3.2rem 2rem 5.6rem;
	background-color: #ebdcff;
	text-align: center;
	&-icon {
		width: 4rem;
		height: 4rem;
		border-radius: 4px;
		background-color: #ffffff;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 auto 0.8rem; }
	&-box {
		margin: {
			top: 2.4rem;
			bottom: 2.5rem; }
		display: flex;
		margin: {
			left: -.8rem;
			right: -.8rem; }
		> * {
			padding: {
				left: 0.35rem;
				right: 0.35rem; }
