				height: 5.2rem; } }
		&_title {
			display: block;
			font-weight: 700;
			margin-top: 1.2rem; } } }
// premium

// country
.country {
	padding: {
		left: 2rem;
		right: 2rem; }
	.section-text p {
		display: none; }
	&-box {
		margin-bottom: 3.2rem; }
	&-block {
		position: relative;
		&:not(:first-child) {
