		&_title {
			text-transform: uppercase; }
		&_links {
			display: flex;
			position: absolute;
			right: 1.2rem;
			bottom: 1.2rem;
			a {
				border-radius: .4rem;
				border: .15rem solid #ffffff;
				color: #ffffff;
				display: flex;
				align-items: center;
				justify-content: center;
				height: 3.8rem;
				padding: {
					left: 2rem;
					right: 2rem; }
				&:not(:first-child) {
					margin-left: 0.8rem; }
				&:last-child {
					display: none; } } } } }
// country

// reviews
.reviews {
	margin-bottom: 4.8rem;
	padding: {
		left: 2rem;
		right: 2rem; }
	.heading {
		.rating-list {
			margin-left: 1rem;
			margin-right: 1rem; }
		.rating, .h3 {
			flex: 0 0 100%;
			width: 100%;
			* {
				margin-bottom: 0; } } }
	&-box {
		display: flex;
		overflow-x: auto;
		margin: {
			left: -2rem;
			right: -2rem; }
		padding: {
			top: 2rem;
			bottom: 2rem; }
		> *:not(:first-child) {
			margin-left: 1.2rem; }
		&:after, &:before {
