			max-width: 2rem;
			min-height: .1rem; } }
	&-block {
		max-width: 33.5rem;
		flex: 0 0 33.5rem;
		border-radius: .8rem;
		box-shadow: 0 .1rem .7rem 0 rgba(0, 0, 0, 0.1);
		padding: 2rem;
		background-color: #ffffff; }
	.author {
		margin: {
			top: -3.6rem;
			bottom: 2rem; }
		align-items: flex-end;
		&-image {
			margin-right: 2rem; } } }
// reviews

// insta
.insta {
	text-align: center;
	overflow: hidden;
	margin-bottom: 4.8rem;
	.h3 {
		margin-bottom: 0.4rem; }
	p {
		font-size: 1.6rem; }
	.section-content {
		> .text-center {
