// insta

// about
.about {
	padding: 4rem 2rem 0;

	background-color: rgba(0, 179, 240, 0.1);
	text-align: center;
	overflow: hidden;
	margin-bottom: 4.8rem;
	.btn {
		margin-top: 2.4rem;
		margin-bottom: 2.4rem; }
	&-img {
		margin: {
			left: -2rem;
			right: -2rem; }
		img {
			@include img-fluid;
			width: 100%; } } }
// about

// blog
.blog {
	padding: {
		left: 2rem;
		right: 2rem; }
	margin-bottom: 4.8rem;
	&-box {
		display: flex;
		overflow-x: auto;
		padding: {
			bottom: 2rem; }
		margin: {
			right: -2rem;
			left: -2rem; }
		> *:not(:first-child) {
			margin-left: 1.6rem; }
		&:after, &:before {
			content: '';
			display: block;
			flex: 0 0 2rem;
			max-width: 2rem;
			min-height: .1rem; } }
	&-block {
