// footer
.footer {
	.container {
		padding: {
			left: 2rem;
			right: 2rem; } }
	&-box > .footer-block:last-child {
		border-bottom: none; }
	&-block {
		border-bottom: .1rem solid rgba(6, 16, 88, 0.1);
		&:not(.newsletter) {
			> .footer-block_title {
				position: relative;
				&:after {
					content: '';
					display: block;
					width: 1.2rem;
					height: 0.7rem;
					background: url(../img/data/toggle.svg) center no-repeat;
					position: absolute;
					right: 0;
					top: 50%;
					transform: translateY(-50%); } } }
		&_title {
			padding: {
				top: 1.5rem;
				bottom: 1.5rem; }
			margin-bottom: 0;
			+ * {
				display: none;
				padding-bottom: 2.4rem; } }
		&.newsletter {
			form {
				display: block; }
			.form-control {
				border: solid 1px rgba(6, 16, 88, 0.1);
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
				width: calc(100% + .4rem);
				margin-right: -0.4rem; } } }
	&-copy {
		background-color: #e5f7fd;
		text-align: center;
		font-size: 1.3rem;
		padding: {
			top: 1rem;
			bottom: 0.9rem; }
		display: block;
		line-height: 1.46;
		a {
			color: $text; } } }
.payment {
	margin-top: 2.8rem;
	&-info {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		&_text {
			flex: 0 0 50%;
			max-width: 50%; } }
	&-card {
		display: flex;
		justify-content: space-between;
		flex: 100%;
		max-width: 100%;
